.header__top {
  padding: 24px 0 25px;
  background: #202020;
}
.header__menu-container {
  padding: 0;
}
.header__right {
  display: none;
}
.header__close {
  visibility: hidden;
  position: absolute;
  opacity: 0;
  padding: 6px;
  width: 23px;
  height: 23px;
  border-radius: 100%;
  background: #C4C4C4;
  transition: opacity 0.5s ease, visibility 0.5s ease;
}
.header__close::after, .header__close::before {
  content: "";
  position: absolute;
  width: 13.8px;
  height: 1.15px;
  top: 50%;
  transform: translateY(-50%);
  background: #fff;
  transition: background 0.3s ease;
}
.header__close::after {
  left: 5px;
  transform: rotate(-45deg);
}
.header__close::before {
  right: 4px;
  transform: rotate(45deg);
}
.header__close:focus-visible {
  outline: none;
}
.header__close:focus-visible::after, .header__close:focus-visible::before {
  background: #BC572B;
}
.header__close:hover::after, .header__close:hover::before {
  background: #FF6E30;
}
.header__close:active::after, .header__close:active::before {
  background: #CE4307;
}
.header__close_active {
  visibility: visible;
  opacity: 1;
}
.header-bottom__nav {
  display: none;
}
.header__bottom {
  padding: 12px 0 11px;
  background: #4A4A4A;
}
.header__container {
  display: flex;
  align-items: center;
}
.header__container.header-bottom__container {
  justify-content: space-between;
}
.header__logo {
  margin-right: 10.673%;
  width: 14.6346154%;
}
.header__nav {
  max-width: 742px;
  width: 100%;
  margin-right: 6.76923%;
}
.header__link {
  position: relative;
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
  color: #FFFFFF;
  transition: color 0.3s ease;
  z-index: 100;
}
.header__link::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  top: -5px;
  right: -5px;
  bottom: -5px;
  left: -5px;
  background: #BC572B;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}
.header__link:focus-visible {
  outline: none;
  color: #ffffff;
}
.header__link:focus-visible::after {
  opacity: 1;
}
.header__link:hover {
