.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  display: block;
  padding: 175px 50px 150px 60px;
  background: rgba(32, 32, 32, 0.85);
  z-index: 1000;
  transition: transform 0.3s ease;
}
.overlay_hide {
  transform: translateX(-360px);
}
.overlay__close {
  position: absolute;
  top: 20px;
  right: 20px;
  border-radius: 100%;
  width: 20px;
  height: 20px;
  background: #666666;
  cursor: pointer;
}
.overlay__close::after, .overlay__close::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  top: 9.5px;
  right: auto;
  bottom: auto;
  left: auto;
  width: 12px;
  height: 1px;
  background: #ffffff;
  transition: transform 0.3s ease, background 0.3s ease;
}
.overlay__close::after {
  transform: rotate(45deg);
  left: 4px;
}
.overlay__close::before {
  transform: rotate(-45deg);
  right: 4px;
}
.overlay__close:focus-visible {
  outline: none;
}
.overlay__close:focus-visible::after, .overlay__close:focus-visible::before {
  background: #BC572B;
}
.overlay__close:hover::after, .overlay__close:hover::before {
  background: #FF6E30;
}
.overlay__close:active::after, .overlay__close:active::before {
  background: #CE4307;
}
.overlay__close_open::after {
  transform: translate(1.5px, -3px) scale(0.75) rotate(45deg);
}
.overlay__close_open::before {
  transform: translate(1px, 3px) scale(0.75) rotate(-45deg);
}
